<template>
  <div class="main"> 
    <div class="wrapper">
      <p style="color:white;font-size:60px;">Thanks to finish our questionnaire!</p>
      <span style="color:white;font-size:20px;">& Your verify code is : {{code}}</span>
      <p style="color:white;font-size:20px;">( Copy and submit it on Amazon MTurk  ~ )</p>
    </div>
  </div>
</template>

<script>
  // const ipAddress='http://127.0.0.1:8888/';
  // const ipAddress='http://47.100.226.85/ques/';
  const ipAddress = 'http://127.0.0.1:8888/'
  import axios from 'axios';
  export default {
  name: 'finish',
  data () {
    return {
       code:'',
       isRepeated:false,
    }
  },
  created() {
    const sheetIndex = window.localStorage.getItem('sheetIndex');
    axios.post(ipAddress +'/answer/code ',
      {
        "sheetIndex":sheetIndex,  // 问卷编号
      }
      ).then((res)=>{
        this.code = res.data.code;
        this.isRepeated = res.data.isRepeated;
        if(this.isRepeated){
          alert('Each paper can only get one verify code!')
        }
    }).catch(function (error) {
        console.log(error);
    });
  } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main{
  height: 100%;
  width:100%;
  background-image: url(../assets/banner.jpg);
}
.wrapper{
  width:100%;
  position: absolute;
  top:50%;
  transform:translateY(-60%);
  text-align: center;
}
.button{
  width:200px;
  height:60px;
  outline: none;
  border-radius: 10px;
  font-size:25px;
  color:white;
  background-color:transparent
}
</style>



