<template>
  <div class="main">
    <div v-if="bigFg==true">
      <div class="question" style="width:90%;margin:20px">
          <span style="line-height: 1.4;">
              On the left, you see an indoor scene (background), part of which is occluded by a black rectangle.
              Your task is to determine for each of the objects on the right how well they may fit in place of the black rectangle, in terms of pose and style (ignore scale mismatch).
          </span>
      </div>
      <div class="option-img">
        <div class="bg">
          <img :src="qsList.paper[index].bgURL">
        </div>
        <div :class="scale==true?'fg big': 'fg big bigger'" @click="scale=!scale">
          <img :src="qsList.paper[index].tnURL">
        </div>
      </div>
    </div>
    <div v-else>
      <div class="question">
        <span>{{`Question ${ index+1}.${smIndex+1} : how well does the`}}</span> <span style="color:red;font-size:26px">{{ qsList.paper[index].category}}</span><span> {{`on the right fit the room on the left ？`}}</span>
      </div>
      <div class="option-img">
        <div class="bg">
          <img :src="qsList.paper[index].tests[smIndex].bgURL">
        </div>
        <div class="fg">
          <img :src="qsList.paper[index].tests[smIndex].fgURL">
        </div>
      </div>
      <div class="radio">
        <span style="font-size:20px;font-weight:bold;margin-bottom:5px;display:block">select an option:</span>
        <div :class="picked==4?'current':''">
          <input type="radio" id='four' value="4" v-model="picked" style="">
          <label for="four">Can crop the object out, and paste it directly</label>
        </div>
        <div :class="picked==3?'current':''">
          <input type="radio" id='three' value="3" v-model="picked">
          <label for="three">Need to modify <span style="color:red;">one</span> property, such as viewpoint <span style="color:red;">or</span> color</label>
        </div>
        <div :class="picked==2?'current':''">
          <input type="radio" id='two' value="2" v-model="picked">
          <label for="two">Need to modify <span style="color:red;">multiple</span> properties, such as viewpoint <span style="color:red;">and</span> color</label>
        </div>
        <div :class="picked==1?'current':''">
          <input type="radio" id='one' value="1" v-model="picked">
          <label for="one">No way</label>
        </div>
      </div>
    </div>
    <button @click='next' class="button">next</button>
  </div>
</template>

<script>
    // const ipAddress = 'http://127.0.0.1:8888/';
    // const ipAddress = 'http://47.100.226.85/ques/';
    const ipAddress = 'http://127.0.0.1:8888/'
    import axios from 'axios';

    export default {
        name: 'getstart',
        data() {
            return {
                scale: true,
                bigFg: true,
                timeStamp: 0,
                picked: '',
                qsList: {},
                index: 0,
                smIndex: 0,
            }
        },
        beforeRouteLeave: (to, from, next) => {
            const sheetIndex = window.localStorage.getItem('sheetIndex');
            if (sheetIndex) {
                next();
            } else {
                alert('you should finish the paper to get a verify code')
                next(false);
            }
        },
        methods: {
            next() {
                if (this.bigFg) {
                    this.bigFg = false;
                } else {
                    if (this.smIndex === this.qsList.paper[this.index].tests.length) {
                        this.smIndex = 0;
                        this.index++;
                        if (this.index >= this.qsList.paper.length) {
                            window.localStorage.setItem('sheetIndex', this.qsList.sheetIndex);
                            this.$router.push('/finish');
                        } else {
                            this.bigFg = true;
                        }
                    } else {
                        if (!this.picked) {
                            alert('Sorry,please give us your answer.');
                        } else {
                            let now = new Date().getTime();
                            let costTime = now - this.timeStamp;
                            this.timeStamp = now;
                            axios.post(ipAddress + 'answer/add',
                                {
                                    "sheetIndex": this.qsList.sheetIndex + '',  // 问卷编号
                                    "bgIndex": this.qsList.paper[this.index].tests[this.smIndex].bgIndex,  // 题目编号
                                    "fgIndex": this.qsList.paper[this.index].tests[this.smIndex].fgIndex,
                                    "answer": this.picked - 1,  // 答案
                                    "duration": costTime + '', // 作答时间
                                    "category": this.qsList.paper[this.index].category
                                }
                            ).then((res) => {
                                console.log(res.data);
                            }).catch(function (error) {
                                console.log(error);
                            });
                            this.smIndex++;
                            this.picked = '';
                        }
                    }
                }
            }
        },
        created() {
            window.localStorage.removeItem('sheetIndex');
            this.timeStamp = new Date().getTime();
            axios.get(ipAddress + 'question/paper').then((res) => {
                this.qsList = res.data;
            }).catch(function (error) {
                console.log(error);
            });
        }
    }
</script>


<style scoped>
  @media screen and (min-width: 768px) {
    .main {
      width: 80%;
      background-color: #f2f3f3;
      margin: 0 auto;
    }
  }

  @media screen and (max-width: 768px) {

    .main {
      width: 100%;
      height: 100%;
      background-color: #f2f3f3;
      margin: 0 auto;
    }

    .question {
      width: 50%;
      font-size: 20px !important;
    }

    .option-img {
      display: flex;
      flex-direction: row;
    }

    .option-img .bg {
      flex: 2;
      margin-right: 10px;
    }

    .option-img .fg {
      flex: 1;
    }

    .option-img img {
      width: 100%;
      height: 100%;
    }
  }

  .main {
    padding: 20px 20px;
  }

  .question {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  @media screen and (min-width: 768px) {
    .option-img {
      display: flex;
      width: 90%;
    }

    .option-img .bg {
      height: 320px;
      margin-right: 20px;
      padding: 20px;
      background-color: #fff;
    }

    .big {
      position: relative;
    }

    .big:hover {
      cursor: zoom-in;
    }

    .bigger {
      transform: scale(2) translate(-80px, 60px);
    }

    .bigger:hover {
      cursor: zoom-out;
    }

    .option-img .fg {
      height: 320px;
      padding: 20px;
      background-color: #fff;
    }

    .option-img .fg img {
      height: 100%;
      max-width: 100%;
    }

    .option-img .bg img {
      height: 100%;
      max-width: 100%;
    }
  }

  .radio label {
    font-size: 18px;
    display: inline-block;
    width: 650px;
  }

  .current {
    color: #4c89f5;
    font-weight: bold;
  }

  .button {
    margin-top: 20px;
    width: 100px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 10px;
    font-size: 20px;
    color: white;
    background-color: #4c89f5
  }

  .radio {
    margin-top: 20px;
  }

  .radio div {
    width:700px;
    border: 1px solid #e1e3e3;
    padding: 8px 15px;
    background-color: #fff;
  }

  .radio div:nth-child(2), .radio div:nth-child(3), .radio div:nth-child(4) {
    border-bottom: none;
  }
</style>



