<template>
  <div class="main"> 
    <div class="wrapper">
      <p style="color:white">Welcome to our questionnaire!</p>
      <button  class="button" @click="getStarted">get started</button>
    </div>
  </div>
</template>

<script>

  export default {
  name: 'index',
  data () {
    return {
       
    }
  },
  methods:{
    getStarted(){
       this.$router.push('/questionaire');
    }
  },
  created(){
    window.localStorage.removeItem('sheetIndex');
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main{
  height: 100%;
  width:100%;
  background-image: url(../assets/banner.jpg);
}
.wrapper{
  width:100%;
  position: absolute;
  top:50%;
  transform:translateY(-60%);
  text-align: center;
  font-size:60px;
}
.button{
  width:200px;
  height:60px;
  outline: none;
  border-radius: 10px;
  font-size:25px;
  color:white;
  background-color:transparent
}
</style>



